.o-dot {
  position: absolute;
  display: block;
  min-width: 20px;
  min-height: 20px;
  inset-block-start: 30%;
  inset-inline-start: 50%;
  margin: -8px 0 0 -8px;
  background: #ffffff;
  border-radius: 100%;
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.25);
  cursor: pointer;
  z-index: 1;
  transform: scale(1.3);
  transition-property: transform;
  transition-duration: 0.25s;
  transition-timing-function: ease-in-out;
  transition-delay: 0s;
  transition: all 0.25s ease-in-out;
  animation: 1.4s ease-in-out infinite;
}
@media (max-width: 600px) {
  .o-dot {
    transform: scale(1);
  }
  .o-dot:hover {
    transform: scale(1.2);
  }
}
.o-dot:after {
  position: absolute;
  content: "";
  min-width: 40px;
  min-height: 40px;
  inset-inline-start: -10px;
  inset-block-start: -10px;
  border-radius: 100%;
  background: rgba(255, 255, 255, 0.4);
}
.o-dot:hover {
  transform: scale(1.5);
}

.o-dot-offer-img-1 {
  inset-block-start: 38%;
  inset-inline-start: 32%;
}

.o-dot-offer-img-2 {
  inset-block-start: 52%;
  inset-inline-start: 63%;
}

.o-dot-offer-img-3 {
  inset-block-start: 38%;
  inset-inline-start: 67%;
}

#go-to-top {
  background-position: center;
  border-radius: 100%;
  padding: 1.2rem;
  background-color: #f6f6f6;
  right: 40px;
  bottom: 160px;
  cursor: pointer;
  position: fixed;
  z-index: 20;
  background-size: 60%;
  background-repeat: no-repeat;
  filter: drop-shadow(0 2px 5px rgba(0, 0, 0, 0.3));
}

.loader {
  position: absolute;
  border: 16px solid #ddd;
  border-radius: 50%;
  -webkit-border-before: 16px solid #c3dd94;
          border-block-start: 16px solid #c3dd94;
  -webkit-padding-after: 16px solid #c3dd94;
          padding-block-end: 16px solid #c3dd94;
  max-inline-size: 120px;
  max-block-size: 120px;
  margin: 0;
  inset-block-start: 50%;
  inset-inline-end: 0;
  inset-block-end: 0;
  inset-inline-start: 50%;
  animation: spin 2s linear infinite;
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.checkout-steps {
